<template>
  <section class="parameters">
    <div class="wrapper-parameters">
      <v-expansion-panels flat multiple variant="accordion">
        <DescriptionBlock @description="getDescription" @exception="getException"></DescriptionBlock>
        <UploadImages @image="getImage"></UploadImages>
        <ResolutionBlock @width="getWidth" @height="getHeight"></ResolutionBlock>
        <GenerationParameters
            @numberOfImage="numberImage"
            @steps="steps"
            @guidanceScale="guidanceScale"
            @seed="seed"
        ></GenerationParameters>
        <Settings></Settings>
      </v-expansion-panels>
    </div>

    <section class="main-buttons">
      <button class="create-project" v-if="person.name" @click="generate">Сгенерировать {{ counterImage }}
        <v-icon end icon="mdi-arrow-right"></v-icon>
      </button>
      <RegistrationDialog @openAcceptDialog="openAcceptDialog" @openRegistrationDialog="toggleDrawer"
                          v-if="!person.name"></RegistrationDialog>
      <AcceptEmailDialog v-if="isOpenAcceptDialog" @closeDialog="closeAcceptDialog"></AcceptEmailDialog>
    </section>
  </section>
</template>

<script setup>

import ResolutionBlock from "@/components/text-to-image/parameters/ResolutionBlock";
import GenerationParameters from "@/components/text-to-image/parameters/GenerationParameters";
import Settings from "@/components/text-to-image/parameters/SettingsBlock"
import DescriptionBlock from "@/components/text-to-image/parameters/DescriptionBlock";
import UploadImages from "@/components/text-to-image/parameters/UploadImages";
import RegistrationDialog from "@/components/dialogs/RegistrationDialog";
import AcceptEmailDialog from "@/components/dialogs/AcceptEmailDialog";
import {personStore} from "@/store/personStore";
import {computed, ref} from "vue";
import {storeToRefs} from "pinia/dist/pinia";
import requests from "@/mixins/requests";

const {generateImage} = requests();

const store = personStore();
const {person} = storeToRefs(store);
let hasHiddenDrawer = ref(true);
let isOpenAcceptDialog = ref(false);
let filters = ref({
  description: '',
  exception: '',
  image: '',
  size: {
    width: '',
    height: ''
  },
  countImages: 1,
});

function openAcceptDialog() {
  isOpenAcceptDialog.value = true;
}

function toggleDrawer() {
  hasHiddenDrawer.value = !hasHiddenDrawer.value;
}

function closeAcceptDialog() {
  isOpenAcceptDialog.value = false;
}

function getDescription() {

}

function getException(data) {
  console.log(data)
}

function getImage(image) {
  console.log(image)
}

function getWidth(width) {
  console.log(width)
}

function getHeight(height) {
  console.log(height)
}

function numberImage(count) {
  filters.value.countImages = count;
}

function steps(step) {
  console.log(step)
}

function guidanceScale() {
}

function seed() {
}

let counterImage = computed(() => {
  if (filters.value.countImages === 1) {
    return `${filters.value.countImages} изображение`;
  } else if (filters.value.countImages > 1 && filters.value.countImages < 5) {
    return `${filters.value.countImages} изображения`;
  } else {
    return `${filters.value.countImages} изображений`;
  }
})

function generate() {
  generateImage(filters.value);
}

</script>

<style scoped lang="scss">

.parameters {
  width: 350px;
  height: calc(100vh - 70px);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #2b2b2be5;
  border-radius: 0 30px 30px 0;
  padding: 30px 0 0 0;
  margin-top: 70px;

  .wrapper-parameters {
    overflow-x: hidden;
    overflow-y: auto;
    scrollbar-gutter: stable;

    &::-webkit-scrollbar {
      width: 2px;
      height: 1000px;
    }

    &::-webkit-scrollbar-track {
      background: #404040E5;
    }
  }

  .main-buttons {
    display: flex;
    align-items: flex-end;
    min-height: 70px;
    border-radius: 0 0 30px 0;
    background: linear-gradient(to left, var(--light-blue), var(--light-pink));
    transition: all 0.3s;

    .create-project,
    .create-account {
      width: 100%;
      height: 100%;
      border-radius: 0 0 30px 0;
      background: linear-gradient(to left, var(--light-blue), var(--light-pink));
    }

    .create-project:hover {
      box-shadow: none
    }

    &:hover {
      box-shadow: 0 0 15px rgba(54, 226, 255, 0.7);
    }
  }
}

@media screen and(max-width: 650px) {
  .parameters {
    width: 100%;
    border-radius: 0;
    height: auto;

    .wrapper-parameters {

    }

    .main-buttons {
      border-radius: 0;
      align-items: center;

      .create-project,
      .create-account {
        border-radius: 0;
      }
    }
  }
}
</style>
