<template>
  <div class="wrapper-images">
    <div class="photo-grid" v-for="image in person.textToImages?.images" :key="image.id">
      <div class="photo-wrapper" v-if="image.id">
        <img class="main-image" :src="require(`@/assets/main-page/${image.img || 'img-YkfgHICXA8YFGEzbWC36kS.jpeg'}`)"
             alt="image example" @click.prevent="openImageDetails(image.img)">
        <div class="wrapper-reuse">
          <img src="../../assets/text-to-image/block-images/refresh.svg" alt="cover">
          <v-tooltip activator="parent" location="bottom">Переиспользовать параметры</v-tooltip>
        </div>
        <div class="wrapper-add-image">
          <img src="../../assets/text-to-image/block-images/add-image.svg" alt="">
          <v-tooltip activator="parent" location="bottom">Переиспользовать изображение</v-tooltip>
        </div>
        <div class="wrapper-arrows">
          <img src="../../assets/text-to-image/block-images/duble-arrows.svg" alt="">
          <v-tooltip activator="parent" location="bottom">Высококачественный</v-tooltip>
        </div>
        <div class="wrapper-smile">
          <img src="../../assets/text-to-image/block-images/smile.svg" alt="smile">
          <v-tooltip activator="parent" location="bottom">Изменение лица</v-tooltip>
        </div>
        <div class="wrapper-bin" @click="deleteImg(image.id)">
          <svg viewBox="0 0 24 24" fill="none">
            <path
                d="M16 6V5.2C16 4.0799 16 3.51984 15.782 3.09202C15.5903 2.71569 15.2843 2.40973 14.908 2.21799C14.4802 2 13.9201 2 12.8 2H11.2C10.0799 2 9.51984 2 9.09202 2.21799C8.71569 2.40973 8.40973 2.71569 8.21799 3.09202C8 3.51984 8 4.0799 8 5.2V6M10 11.5V16.5M14 11.5V16.5M3 6H21M19 6V17.2C19 18.8802 19 19.7202 18.673 20.362C18.3854 20.9265 17.9265 21.3854 17.362 21.673C16.7202 22 15.8802 22 14.2 22H9.8C8.11984 22 7.27976 22 6.63803 21.673C6.07354 21.3854 5.6146 20.9265 5.32698 20.362C5 19.7202 5 18.8802 5 17.2V6"
                stroke="black" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
          </svg>
        </div>
        <div class="wrapper-like" @click="toggleLike(image.id)">
          <svg class="like" :class="{'like-active': image.like}"
               xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"
               stroke-width="2"
               stroke-linecap="round" stroke-linejoin="round">
            <path
                d="M20.84 4.61a5.5 5.5 0 0 0-7.78 0L12 5.67l-1.06-1.06a5.5 5.5 0 0 0-7.78 7.78l1.06 1.06L12 21.23l7.78-7.78 1.06-1.06a5.5 5.5 0 0 0 0-7.78z"></path>
          </svg>
        </div>
      </div>

      <div v-if="!image.id" class="loader-block">
        <div class="spinner">
          <div class="spinner1"></div>
        </div>
      </div>
    </div>
    <ImageDetails v-if="imageDetailsDialog"
                  :image-src="currentImageSrc"
                  @close="imageDetailsDialog = false"></ImageDetails>
  </div>
</template>

<script setup>
import {ref} from "vue";
import ImageDetails from "@/components/dialogs/ImageDetails";
import {storeToRefs} from "pinia/dist/pinia";
import {personStore} from "@/store/personStore";

const store = personStore();
const {person} = storeToRefs(store)
const {toggleFavorite, deleteImage} = store;
let imageDetailsDialog = ref(false);
let currentImageSrc = ref('');

function toggleLike(id) {
  toggleFavorite(id)
}

function deleteImg(id) {
  deleteImage(id);
}

function openImageDetails(image) {
  imageDetailsDialog.value = true;
  currentImageSrc.value = image;
}

</script>

<style scoped lang="scss">
@import "@/style/loader.scss";

.wrapper-images {
  width: calc(100vw - 356px);
  height: 100vh;
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
  gap: 5px;
  overflow: auto;
  padding: 70px 0 0 15px;
  scrollbar-gutter: stable;

  .photo-grid {
    position: relative;
    display: flex;
    width: calc(20% - 4px);
    height: 350px;

    .photo-wrapper {
      width: 100%;
    }

    .main-image,
    .loader-block {
      width: 100%;
      height: 100%;
      border-radius: 20px;
      object-fit: cover;
    }

    .loader-block {
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: #2b2b2be5;

      .spinner {
        background-image: linear-gradient(rgb(186, 66, 255) 35%, rgb(0, 225, 255));
        width: 100px;
        height: 100px;
        animation: spinning82341 1.7s linear infinite;
        text-align: center;
        border-radius: 50px;
        filter: blur(1px);
        box-shadow: 0px -5px 20px 0px rgb(186, 66, 255), 0px 5px 20px 0px rgb(0, 225, 255);
      }

      .spinner1 {
        background-color: rgb(36, 36, 36);
        width: 100px;
        height: 100px;
        border-radius: 50px;
        filter: blur(10px);
      }

      @keyframes spinning82341 {
        to {
          transform: rotate(360deg);
        }
      }
    }

    .wrapper-like,
    .wrapper-bin,
    .wrapper-reuse,
    .wrapper-smile,
    .wrapper-add-image,
    .wrapper-arrows {
      position: absolute;
      display: flex;
      opacity: 0;
      justify-content: center;
      padding: 10px;
      border-radius: 5px;
      background-color: rgba(31, 32, 35, 0.6);
      backdrop-filter: blur(5px);
      -webkit-backdrop-filter: blur(5px);
      cursor: pointer;

      &:hover {
        background-color: rgba(31, 32, 35, 0.7);
      }
    }

    .wrapper-reuse {
      top: 20px;
      left: 20px;
    }

    .wrapper-add-image {
      top: 20px;
      left: 60px;
    }

    .wrapper-arrows {
      top: 20px;
      right: 60px;
      transform: rotate(-90deg);
    }

    .wrapper-smile {
      top: 20px;
      right: 20px;
    }

    .wrapper-bin {
      bottom: 20px;
      left: 20px;

      svg {
        width: 14px;

        path {
          stroke: #FD4D4DFF;
        }
      }
    }

    .wrapper-like {
      bottom: 20px;
      right: 20px;

      .like {
        width: 14px;
        stroke: var(--light-blue);
        fill: none;
      }

      .like-active {
        stroke: #FD4D4DFF;
        fill: #FD4D4DFF;
      }
    }

    &:hover {
      .wrapper-like,
      .wrapper-bin,
      .wrapper-reuse,
      .wrapper-smile,
      .wrapper-add-image,
      .wrapper-arrows {
        opacity: 1;
      }
    }
  }
}

@media screen and(max-width: 1500px) {
  .wrapper-images {

    .photo-grid {
      width: calc(25% - 4px);
    }
  }
}

@media screen and(max-width: 1200px) {
  .wrapper-images {

    .photo-grid {
      width: calc(33% - 4px);
    }
  }
}

@media screen and(max-width: 1000px) {
  .wrapper-images {

    .photo-grid {
      width: calc(50% - 4px);

      & {
        .wrapper-like,
        .wrapper-bin,
        .wrapper-reuse,
        .wrapper-smile,
        .wrapper-add-image,
        .wrapper-arrows {
          opacity: 1;
        }
      }
    }
  }
}

@media screen and(max-width: 800px) {
  .wrapper-images {
    .photo-grid {
      width: calc(100% - 4px);
    }
  }
}

@media screen and(max-width: 650px) {
  .wrapper-images {
    overflow: unset;
    height: auto;
    width: 100%;
    padding: 30px 10px;
  }
}

</style>
